import { Block, ValueToTextRule } from '@ali/4ever-cangjie';
import { Container } from '@ali/4ever-plugin-container';
import { ELEMENT_TYPE } from '../../utils/meta';

const valueToText: ValueToTextRule = {
  name: 'container',

  match(node) {
    return Container.isContainer(node) && node.data.subType === ELEMENT_TYPE;
  },

  convert(node, convertor) {
    return (node as Block).nodes.map((n) => convertor(n)).join('');
  },
};

export default valueToText;
